e: 24px;\n  }\n\n  &:before {\n    content: '\\E5CC';\n    color: rgba(255,255,255, .7);\n    vertical-align: top;\n    display: inline-block;\n    font-family: 'Material Icons';\n    font-weight: normal;\n    font-style: normal;\n    font-size: 25px;\n    margin: 0 10px 0 8px;\n    -webkit-font-smoothing: antialiased;\n  }\n\n  &:first-child:before {\n    display: none;\n  }\n\n  &:last-child {\n    color: #fff;\n  }\n}\n\n// Parallax\n.parallax-container {\n  position: relative;\n  overflow: hidden;\n  height: 500px;\n\n  .parallax {\n    position: absolute;\n    top: 0;\n    left: 0;\n    right: 0;\n    bottom: 0;\n    z-index: -1;\n\n    img {\n      opacity: 0;\n      position: absolute;\n      left: 50%;\n      bottom: 0;\n      min-width: 100%;\n      min-height: 100%;\n      transform: translate3d(0,0,0);\n      transform: translateX(-50%);\n    }\n  }\n}\n\n// Pushpin\n.pin-top, .pin-bottom {\n  position: relative;\n}\n.pinned {\n  position: fixed !important;\n}\n\n/*********************\n  Transition Classes\n**********************/\n\nul.staggered-list li {\n  opacity: 0;\n}\n\n.fade-in {\n  opacity: 0;\n  transform-origin: 0 50%;\n}\n\n\n/*********************\n  Media Query Classes\n**********************/\n.hide-on-small-only, .hide-on-small-and-down {\n  @media #{$small-and-down} {\n    display: none !important;\n  }\n}\n.hide-on-med-and-down {\n  @media #{$medium-and-down} {\n    display: none !important;\n  }\n}\n.hide-on-med-and-up {\n  @media #{$medium-and-up} {\n    display: none !important;\n  }\n}\n.hide-on-med-only {\n  @media only screen and (min-width: $small-screen) and (max-width: $medium-screen) {\n    display: none !important;\n  }\n}\n.hide-on-large-only {\n  @media #{$large-and-up} {\n    display: none !important;\n  }\n}\n.hide-on-extra-large-only {\n  @media #{$extra-large-and-up} {\n    display: none !important;\n  }\n}\n.show-on-extra-large {\n  @media #{$extra-large-and-up} {\n    display: block !important;\n  }\n}\n.show-on-large