display: block;\n    position: relative;\n\n    td:empty:before {\n      content: '\\00a0';\n    }\n\n    th,\n    td {\n      margin: 0;\n      vertical-align: top;\n    }\n\n    th { text-align: left; }\n    thead {\n      display: block;\n      float: left;\n\n      tr {\n        display: block;\n        padding: 0 10px 0 0;\n\n        th::before {\n          content: \"\\00a0\";\n        }\n      }\n    }\n    tbody {\n      display: block;\n      width: auto;\n      position: relative;\n      overflow-x: auto;\n      white-space: nowrap;\n\n      tr {\n        display: inline-block;\n        vertical-align: top;\n      }\n    }\n    th {\n      display: block;\n      text-align: right;\n    }\n    td {\n      display: block;\n      min-height: 1.25em;\n      text-align: left;\n    }\n    tr {\n      border-bottom: none;\n      padding: 0 10px;\n    }\n\n    /* sort out borders */\n    thead {\n      border: 0;\n      border-right: 1px solid $table-border-color;\n    }\n  }\n\n}\n\n\n// Collections\n.collection {\n  margin: $element-top-margin 0 $element-bottom-margin 0;\n  border: 1px solid $collection-border-color;\n  border-radius: 2px;\n  overflow: hidden;\n  position: relative;\n\n  .collection-item {\n    background-color: $collection-bg-color;\n    line-height: $collection-line-height;\n    padding: 10px 20px;\n    margin: 0;\n    border-bottom: 1px solid $collection-border-color;\n\n    // Avatar Collection\n    &.avatar {\n      min-height: 84px;\n      padding-left: 72px;\n      position: relative;\n\n      // Don't style circles inside preloader classes.\n      &:not(.circle-clipper) > .circle,\n      :not(.circle-clipper) > .circle {\n        position: absolute;\n        width: 42px;\n        height: 42px;\n        overflow: hidden;\n        left: 15px;\n        display: inline-block;\n        vertical-align: middle;\n      }\n      i.circle {\n        font-size: 18px;\n        line-height: 42px;\n        color: #fff;\n        background-color: #999;