media #{$large-and-up} {\n\n      $i: 1;\n      @while $i <= $num-cols {\n        $perc: unquote((100 / ($num-cols / $i)) + \"%\");\n        &.l#{$i} {\n          width: $perc;\n          @include reset-offset;\n        }\n        $i: $i + 1;\n      }\n\n      $i: 1;\n      @while $i <= $num-cols {\n        $perc: unquote((100 / ($num-cols / $i)) + \"%\");\n        @include grid-classes(\"l\", $i, $perc);\n        $i: $i + 1;\n      }\n    }\n\n    @media #{$extra-large-and-up} {\n\n      $i: 1;\n      @while $i <= $num-cols {\n        $perc: unquote((100 / ($num-cols / $i)) + \"%\");\n        &.xl#{$i} {\n          width: $perc;\n          @include reset-offset;\n        }\n        $i: $i + 1;\n      }\n\n      $i: 1;\n      @while $i <= $num-cols {\n        $perc: unquote((100 / ($num-cols / $i)) + \"%\");\n        @include grid-classes(\"xl\", $i, $perc);\n        $i: $i + 1;\n      }\n    }\n  }\n}\n","nav {\n  &.nav-extended {\n    height: auto;\n\n    .nav-wrapper {\n      min-height: $navbar-height-mobile;\n      height: auto;\n    }\n\n    .nav-content {\n      position: relative;\n      line-height: normal;\n    }\n  }\n\n  color: $navbar-font-color;\n  @extend .z-depth-1;\n  background-color: $primary-color;\n  width: 100%;\n  height: $navbar-height-mobile;\n  line-height: $navbar-line-height-mobile;\n\n  a { color: $navbar-font-color; }\n\n  i,\n  [class^=\"mdi-\"], [class*=\"mdi-\"],\n  i.material-icons {\n    display: block;\n    font-size: 24px;\n    height: $navbar-height-mobile;\n    line-height: $navbar-line-height-mobile;\n  }\n\n  .nav-wrapper {\n    position: relative;\n    height: 100%;\n  }\n\n  @media #{$large-and-up} {\n    a.sidenav-trigger { display: none; }\n  }\n\n\n  // Collapse button\n  .sidenav-trigger {\n    float: left;\n    position: relative;\n    z-index: 1;\n    height: $navbar-height-mobile;\n    margin: 0 18px;\n\n    i {\n      height: $navbar-height-mobile;\n      line-height: $navbar-line-height-mobile;\n    }\n  }\n\n\n  /