\n}\n\n// Raised Button\n.btn {\n  text-decoration: none;\n  color: $button-raised-color;\n  background-color: $button-raised-background;\n  text-align: center;\n  letter-spacing: .5px;\n  @extend .z-depth-1;\n  transition: background-color .2s ease-out;\n  cursor: pointer;\n  &:hover {\n    background-color: $button-raised-background-hover;\n    @extend .z-depth-1-half;\n  }\n}\n\n// Floating button\n.btn-floating {\n  &:hover {\n    background-color: $button-floating-background-hover;\n    @extend .z-depth-1-half;\n  }\n  &:before {\n    border-radius: 0;\n  }\n  &.btn-large {\n    &.halfway-fab {\n      bottom: -$button-floating-large-size / 2;\n    }\n    width: $button-floating-large-size;\n    height: $button-floating-large-size;\n    padding: 0;\n    i {\n      line-height: $button-floating-large-size;\n    }\n  }\n\n  &.btn-small {\n    &.halfway-fab {\n      bottom: -$button-floating-small-size / 2;\n    }\n    width: $button-floating-small-size;\n    height: $button-floating-small-size;\n    i {\n      line-height: $button-floating-small-size;\n    }\n  }\n\n  &.halfway-fab {\n    &.left {\n      right: auto;\n      left: 24px;\n    }\n    position: absolute;\n    right: 24px;\n    bottom: -$button-floating-size / 2;\n  }\n  display: inline-block;\n  color: $button-floating-color;\n  position: relative;\n  overflow: hidden;\n  z-index: 1;\n  width: $button-floating-size;\n  height: $button-floating-size;\n  line-height: $button-floating-size;\n  padding: 0;\n  background-color: $button-floating-background;\n  border-radius: $button-floating-radius;\n  @extend .z-depth-1;\n  transition: background-color .3s;\n  cursor: pointer;\n  vertical-align: middle;\n  i {\n    width: inherit;\n    display: inline-block;\n    text-align: center;\n    color: $button-floating-color;\n    font-size: $button-large-icon-font-size;\n    line-height: $button-floating-size;\n  }\n}\n\n// button fix\nbutton.btn-floating {\n  border: $button-border;\n}\n\n// Fixed Action Button\n.fixed-action-btn {\n  &.active {\n    ul {\n      visibility: visible;\n    }\n  }\n\n  // Directions\n  &.direction-left,\n  &.direction-right {\n    padding: 0 0 0 15px;\n    ul {\n      text-align: right;\n      right: 64px;\n      top: 50%;\n      transform: translateY(-50%);\n      height: 100%;\n      left: auto;\n      /*width 100% only goes to width of button container */\n      width: 500px;\n      li {\n        display: inline-block;\n        margin: 7.5px 15px 0 0;\n      }\n    }\n  }\n  &.direction-right {\n    padding: 0 15px 0 0;\n    ul {\n      text-align: left;\n      direction: rtl;\n      left: 64px;\n      right: auto;\n      li {\n        margin: 7.5px 0 0 15px;\n      }\n    }\n  }\n  &.direction-bottom {\n    padding: 0 0 15px 0;\n    ul {\n      top: 64px;\n      bottom: auto;\n      display: flex;\n      flex-direction: column-reverse;\n      li {\n        margin: 15px 0 0 0;\n      }\n    }\n  }\n  &.toolbar {\n    &.active {\n      &>a i {\n        opacity: 0;\n      }\n    }\n    padding: 0;\n    height: $button-floating-large-size;\n    ul {\n      display: flex;\n      top: 0;\n      bottom: 0;\n      z-index: 1;\n      li {\n        flex: 1;\n        display: inline-block;\n        margin: 0;\n        height: 100%;\n        transition: none;\n        a {\n          display: block;\n          overflow: hidden;\n          position: relative;\n          width: 100%;\n          height: 100%;\n          background-color: transparent;\n          box-shadow: none;\n          color: #fff;\n          line-height: $button-floating-large-size;\n          z-index: 1;\n          i {\n            line-height: inherit;\n          }\n        }\n      }\n    }\n  }\n  position: fixed;\n  right: 23px;\n  bottom: 23px;\n  padding-top: 15px;\n  margin-bottom: 0;\n  z-index: 997;\n  ul {\n    left: 0;\n    right: 0;\n    text-align: center;\n    position: absolute;\n    bottom: 64px;\n    margin: 0;\n    visibility: hidden;\n    li {\n      margin-bottom: 15px;\n    }\n    a.btn-floating {\n      opacity: 0;\n    }\n  }\n  .fab-backdrop {\n    position: absolute;\n    top: 0;\n    left: 0;\n    z-index: -1;\n    width: $button-floating-size;\n    height: $button-floating-size;\n    background-color: $button-floating-background;\n    border-radius: $button-floating-radius;\n    transform: scale(0);\n  }\n}\n\n// Flat button\n.btn-flat {\n  box-shadow: none;\n  background-color: transparent;\n  color: $button-flat-color;\n  cursor: pointer;\n  transition: background-color .2s;\n  &:focus,\n  &:hover {\n    box-shadow: none;\n  }\n  &:focus {\n    background-color: rgba(0, 0, 0, .1);\n  }\n  &.disabled,\n  &.btn-flat[disabled]  {\n    background-color: transparent !important;\n    color: $button-flat-disabled-color !important;\n    cursor: default;\n  }\n}\n\n// Large button\n.btn-large {\n  @extend .btn;\n  height: $button-large-height;\n  line-height: $button-large-height;\n  font-size: $button-large-font-size;\n  padding: 0 28px;\n\n  i {\n    font-size: $button-large-icon-font-size;\n  }\n}\n\n// Small button\n.btn-small {\n  @extend .btn;\n  height: $button-small-height;\n  line-height: $button-small-height;\n  font-size: $button-small-font-size;\n  i {\n    font-size: $button-small-icon-font-size;\n  }\n}\n\n// Block button\n.btn-block {\n  display: block;\n}\n",".dropdown-content {\n  &:focus {\n    outline: 0;\n  }\n\n\n  @extend .z-depth-1;\n  background-color: $dropdown-bg-color;\n  margin: 0;\n  display: none;\n  min-width: 100px;\n  overflow-y: auto;\n  opacity: 0;\n  position: absolute;\n  left: 0;\n  top: 0;\n  z-index: 9999; // TODO: Check if this doesn't break other things\n  transform-origin: 0 0;\n\n\n  li {\n    &:hover, &.active {\n      background-color: $dropdown-hover-bg-color;\n    }\n\n    &:focus {\n      outline: none;\n    }\n\n    &.divider {\n      min-height: 0;\n      height: 1px;\n    }\n\n    & > a, & > span {\n      font-size: 16px;\n      color: $dropdown-color;\n      display: block;\n      line-height: 22px;\n      padding: (($dropdown-item-height - 22) / 2) 16px;\n    }\n\n    & > span > label {\n      top: 1px;\n      left: 0;\n      height: 18px;\n    }\n\n    // Icon alignment override\n    & > a > i {\n      height: inherit;\n      line-height: inherit;\n      float: left;\n      margin: 0 24px 0 0;\n      width: 24px;\n    }\n\n\n    clear: both;\n    color: $off-black;\n    cursor: pointer;\n    min-height: $dropdown-item-height;\n    line-height: 1.5rem;\n    width: 100%;\n    text-align: left;\n  }\n}\n\nbody.keyboard-focused {\n  .dropdown-content li:focus {\n    background-color: darken($dropdown-hover-bg-color, 8%);\n  }\n}\n\n// Input field specificity bugfix\n.input-field.col .dropdown-content [type=\"checkbox\"] + label {\n  top: 1px;\n  left: 0;\n  height: 18px;\n  transform: none;\n}\n\n.dropdown-trigger {\n  cursor: pointer;\n}",".modal {\n  &:focus {\n    outline: none;\n  }\n\n  @extend .z-depth-5;\n\n  display: none;\n  position: fixed;\n  left: 0;\n  right: 0;\n  background-color: #fafafa;\n  padding: 0;\n  max-height: 70%;\n  width: 55%;\n  margin: auto;\n  overflow-y: auto;\n\n  border-radius: 2px;\n  will-change: top, opacity;\n\n  @media #{$medium-and-down} {\n   width: 80%;\n  }\n\n  h1,h2,h3,h4 {\n    margin-top: 0;\n  }\n\n  .modal-content {\n    padding: 24px;\n  }\n  .modal-close {\n    cursor: pointer;\n  }\n\n  .modal-footer {\n    border-radius: 0 0 2px 2px;\n    background-color: #fafafa;\n    padding: 4px 6px;\n    height: 56px;\n    width: 100%;\n    text-align: right;\n\n    .btn, .btn-flat {\n      margin: 6px 0;\n    }\n  }\n}\n.modal-overlay {\n  position: fixed;\n  z-index: 999;\n  top: -25%;\n  left: 0;\n  bottom: 0;\n  right: 0;\n  height: 125%;\n  width: 100%;\n  background: #000;\n  display: none;\n\n  will-change: opacity;\n}\n\n// Modal with fixed action footer\n.modal.modal-fixed-footer {\n  padding: 0;\n  height: 70%;\n\n  .modal-content {\n    position: absolute;\n    height: calc(100% - 56px);\n    max-height: 100%;\n    width: 100%;\n    overflow-y: auto;\n  }\n\n  .modal-footer {\n    border-top: 1px solid rgba(0,0,0,.1);\n    position: absolute;\n    bottom: 0;\n  }\n}\n\n// Modal Bottom Sheet Style\n.modal.bottom-sheet {\n  top: auto;\n  bottom: -100%;\n  margin: 0;\n  width: 100%;\n  max-height: 45%;\n  border-radius: 0;\n  will-change: bottom, opacity;\n}\n",".collapsible {\n  border-top: 1px solid $collapsible-border-color;\n  border-right: 1px solid $collapsible-border-color;\n  border-left: 1px solid $collapsible-border-color;\n  margin: $element-top-margin 0 $element-bottom-margin 0;\n  @extend .z-depth-1;\n}\n\n.collapsible-header {\n  &:focus {\n    outline: 0\n  }\n\n  display: flex;\n  cursor: pointer;\n  -webkit-tap-highlight-color: transparent;\n  line-height: 1.5;\n  padding: 1rem;\n  background-color: $collapsible-header-color;\n  border-bottom: 1px solid $collapsible-border-color;\n\n  i {\n    width: 2rem;\n    font-size: 1.6rem;\n    display: inline-block;\n    text-align: center;\n    margin-right: 1rem;\n  }\n}\n.keyboard-focused .collapsible-header:focus {\n  background-color: #eee;\n}\n\n.collapsible-body {\n  display: none;\n  border-bottom: 1px solid $collapsible-border-color;\n  box-sizing: border-box;\n  padding: 2rem;\n}\n\n// Sidenav collapsible styling\n.sidenav,\n.sidenav.fixed {\n\n  .collapsible {\n    border: none;\n    box-shadow: none;\n\n    li { padding: 0; }\n  }\n\n  .collapsible-header {\n    background-color: transparent;\n    border: none;\n    line-height: inherit;\n    height: inherit;\n    padding: 0 $sidenav-padding;\n\n    &:hover { background-color: rgba(0,0,0,.05); }\n    i { line-height: inherit; }\n  }\n\n  .collapsible-body {\n    border: 0;\n    background-color: $collapsible-header-color;\n\n    li a {\n      padding: 0 (7.5px + $sidenav-padding)\n               0 (15px + $sidenav-padding);\n    }\n  }\n\n}\n\n// Popout Collapsible\n\n.collapsible.popout {\n  border: none;\n  box-shadow: none;\n  > li {\n    box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);\n    // transform: scaleX(.92);\n    margin: 0 24px;\n    transition: margin .35s cubic-bezier(0.250, 0.460, 0.450, 0.940);\n  }\n  > li.active {\n    box-shadow: 0 5px 11px 0 rgba(0, 0, 0, 0.18), 0 4px 15px 0 rgba(0, 0, 0, 0.15);\n    margin: 16px 0;\n    // transform: scaleX(1);\n  }\n}\n",".sidenav {\n  position: fixed;\n  width: $sidenav-width;\n  left: 0;\n  top: 0;\n  margin: 0;\n  transform: translateX(-100%);\n  height: 100%;\n  height: calc(100% + 60px);\n  height: -moz-calc(100%); //Temporary Firefox Fix\n  padding-bottom: 60px;\n  background-color: $sidenav-bg-color;\n  z-index: 999;\n  overflow-y: auto;\n  will-change: transform;\n  backface-visibility: hidden;\n  transform: translateX(-105%);\n\n  @extend .z-depth-1;\n\n  // Right Align\n  &.right-aligned {\n    right: 0;\n    transform: translateX(105%);\n    left: auto;\n    transform: translateX(100%);\n  }\n\n  .collapsible {\n    margin: 0;\n  }\n\n\n  li {\n    float: none;\n    line-height: $sidenav-line-height;\n\n    &.active { background-color: rgba(0,0,0,.05); }\n  }\n\n  li > a {\n    color: $sidenav-font-color;\n    display: block;\n    font-size: $sidenav-font-size;\n    font-weight: 500;\n    height: $sidenav-item-height;\n    line-height: $sidenav-line-height;\n    padding: 0 ($sidenav-padding * 2);\n\n    &:hover { background-color: rgba(0,0,0,.05);}\n\n    &.btn, &.btn-large, &.btn-flat, &.btn-floating {\n      margin: 10px 15px;\n    }\n\n    &.btn,\n    &.btn-large,\n    &.btn-floating { color: $button-raised-color; }\n    &.btn-flat { color: $button-flat-color; }\n\n    &.btn:hover,\n    &.btn-large:hover { background-color: lighten($button-raised-background, 5%); }\n    &.btn-floating:hover { background-color: $button-raised-background; }\n\n    & > i,\n    & > [class^=\"mdi-\"], li > a > [class*=\"mdi-\"],\n    & > i.material-icons {\n      float: left;\n      height: $sidenav-item-height;\n      line-height: $sidenav-line-height;\n      margin: 0 ($sidenav-padding * 2) 0 0;\n      width: $sidenav-item-height / 2;\n      color: rgba(0,0,0,.54);\n    }\n  }\n\n\n  .divider {\n    margin: ($sidenav-padding / 2) 0 0 0;\n  }\n\n  .subheader {\n    &:hover {\n      background-color: transparent;\n    }\n\n    cursor: initial;\n    pointer-events: none;\n    color: rgba(0,0,0,.54);\n    font-size: $sidenav-font-size;\n    font-weight: 500;\n    line-height: $sidenav-line-height;\n  }\n\n  .user-view {\n    position: relative;\n    padding: ($sidenav-padding * 2) ($sidenav-padding * 2) 0;\n    margin-bottom: $sidenav-padding / 2;\n\n    & > a {\n      &:hover { background-color: transparent; }\n      height: auto;\n      padding: 0;\n    }\n\n    .background {\n      overflow: hidden;\n      position: absolute;\n      top: 0;\n      right: 0;\n      bottom: 0;\n      left: 0;\n      z-index: -1;\n    }\n\n    .circle, .name, .email {\n      display: block;\n    }\n\n    .circle {\n      height: 64px;\n      width: 64px;\n    }\n\n    .name,\n    .email {\n      font-size: $sidenav-font-size;\n      line-height: $sidenav-line-height / 2;\n    }\n\n    .name {\n      margin-top: 16px;\n      font-weight: 500;\n    }\n\n    .email {\n      padding-bottom: 16px;\n      font-weight: 400;\n    }\n  }\n}\n\n\n// Touch interaction\n.drag-target {\n  // Right Align\n  &.right-aligned {\n    right: 0;\n  }\n\n  height: 100%;\n  width: 10px;\n  position: fixed;\n  top: 0;\n  z-index: 998;\n}\n\n\n// Fixed Sidenav shown\n.sidenav.sidenav-fixed {\n  // Right Align\n  &.right-aligned {\n    right: 0;\n    left: auto;\n  }\n\n  left: 0;\n  transform: translateX(0);\n  position: fixed;\n}\n\n// Fixed Sidenav hide on smaller\n@media #{$medium-and-down} {\n  .sidenav {\n    &.sidenav-fixed {\n      transform: translateX(-105%);\n\n      &.right-aligned {\n        transform: translateX(105%);\n      }\n    }\n\n    > a {\n      padding: 0 $sidenav-padding;\n    }\n\n    .user-view {\n      padding: $sidenav-padding $sidenav-padding 0;\n    }\n  }\n}\n\n\n.sidenav .collapsible-body > ul:not(.collapsible) > li.active,\n.sidenav.sidenav-fixed .collapsible-body > ul:not(.collapsible) > li.active {\n  background-color: $primary-color;\n  a {\n    color: $sidenav-bg-color;\n  }\n}\n.sidenav .collapsible-body {\n  padding: 0;\n}\n\n\n.sidenav-overlay {\n  position: fixed;\n  top: 0;\n  left: 0;\n  right: 0;\n  opacity: 0;\n  height: 120vh;\n  background-color: rgba(0,0,0,.5);\n  z-index: 997;\n  display: none;\n}\n","// Remove Focus Boxes\nselect:focus {\n  outline: $select-focus;\n}\n\nbutton:focus {\n  outline: none;\n  background-color: $button-background-focus;\n}\n\nlabel {\n  font-size: $label-font-size;\n  color: $input-border-color;\n}\n\n@import 'input-fields';\n@import 'radio-buttons';\n@import 'checkboxes';\n@import 'switches';\n@import 'select';\n@import 'file-input';\n@import 'range';\n","/* Text Inputs + Textarea\n   ========================================================================== */\n\n/* Style Placeholders */\n\n::placeholder {\n  color: $placeholder-text-color;\n}\n\n/* Text inputs */\n\ninput:not([type]),\ninput[type=text]:not(.browser-default),\ninput[type=password]:not(.browser-default),\ninput[type=email]:not(.browser-default),\ninput[type=url]:not(.browser-default),\ninput[type=time]:not(.browser-default),\ninput[type=date]:not(.browser-default),\ninput[type=datetime]:not(.browser-default),\ninput[type=datetime-local]:not(.browser-default),\ninput[type=tel]:not(.browser-default),\ninput[type=number]:not(.browser-default),\ninput[type=search]:not(.browser-default),\ntextarea.materialize-textarea {\n\n  // General Styles\n  background-color: transparent;\n  border: none;\n  border-bottom: $input-border;\n  border-radius: 0;\n  outline: none;\n  height: $input-height;\n  width: 100%;\n  font-size: $input-font-size;\n  margin: $input-margin;\n  padding: $input-padding;\n  box-shadow: none;\n  box-sizing: content-box;\n  transition: box-shadow .3s, border .3s;\n\n  // Disabled input style\n  &:disabled,\n  &[readonly=\"readonly\"] {\n    color: $input-disabled-color;\n    border-bottom: $input-disabled-border;\n  }\n\n  // Disabled label style\n  &:disabled+label,\n  &[readonly=\"readonly\"]+label {\n    color: $input-disabled-color;\n  }\n\n  // Focused input style\n  &:focus:not([readonly]) {\n    border-bottom: 1px solid $input-focus-color;\n    box-shadow: 0 1px 0 0 $input-focus-color;\n  }\n\n  // Focused label style\n  &:focus:not([readonly])+label {\n    color: $input-focus-color;\n  }\n\n  // Hide helper text on data message\n  &.valid ~ .helper-text[data-success],\n  &:focus.valid ~ .helper-text[data-success],\n  &.invalid ~ .helper-text[data-error],\n  &:focus.invalid ~ .helper-text[data-error] {\n    @extend %hidden-text;\n  }\n\n  // Valid Input Style\n  &.valid,\n  &:focus.valid {\n    @extend %valid-input-style;\n  }\n\n  // Custom Success Message\n  &.valid ~ .helper-text:after,\n  &:focus.valid ~ .helper-text:after {\n    @extend %custom-success-message;\n  }\n  &:focus.valid ~ label {\n    color: $input-success-color;\n  }\n\n  // Invalid Input Style\n  &.invalid,\n  &:focus.invalid {\n    @extend %invalid-input-style;\n  }\n\n  // Custom Error message\n  &.invalid ~ .helper-text:after,\n  &:focus.invalid ~ .helper-text:after {\n    @extend %custom-error-message;\n  }\n  &:focus.invalid ~ label {\n    color: $input-error-color;\n  }\n\n  // Full width label when using validate for error messages\n  &.validate + label {\n    width: 100%;\n  }\n\n  // Form Message Shared Styles\n  & + label:after {\n    @extend %input-after-style;\n  }\n}\n\n\n/* Validation Sass Placeholders */\n%valid-input-style {\n  border-bottom: 1px solid $input-success-color;\n  box-shadow: 0 1px 0 0 $input-success-color;\n}\n%invalid-input-style {\n  border-bottom: $input-invalid-border;\n  box-shadow: 0 1px 0 0 $input-error-color;\n}\n%hidden-text {\n  color: transparent;\n  user-select: none;\n  pointer-events: none;\n}\n%custom-success-message {\n  content: attr(data-success);\n  color: $input-success-color;\n}\n%custom-error-message {\n  content: attr(data-error);\n  color: $input-error-color;\n}\n%input-after-style {\n  display: block;\n  content: \"\";\n  position: absolute;\n  top: 100%;\n  left: 0;\n  opacity: 0;\n  transition: .2s opacity ease-out, .2s color ease-out;\n}\n\n\n// Styling for input field wrapper\n.input-field {\n  // Inline styles\n  &.inline {\n    display: inline-block;\n    vertical-align: middle;\n    margin-left: 5px;\n\n    input,\n    .select-dropdown {\n      margin-bottom: 1rem;\n    }\n  }\n\n  // Gutter spacing\n  &.col {\n    label {\n      left: $gutter-width / 2;\n    }\n\n    .prefix ~ label,\n    .prefix ~ .validate ~ label {\n      width: calc(100% - 3rem - #{$gutter-width});\n    }\n  }\n\n  position: relative;\n  margin-top: 1rem;\n  margin-bottom: 1rem;\n\n  & > label {\n    color: $input-border-color;\n    position: absolute;\n    top: 0;\n    left: 0;\n    font-size: 1rem;\n    cursor: text;\n    transition: transform .2s ease-out, color .2s ease-out;\n    transform-origin: 0% 100%;\n    text-align: initial;\n    transform: translateY(12px);\n\n    &:not(.label-icon).active {\n      transform: translateY(-14px) scale(.8);\n      transform-origin: 0 0;\n    }\n  }\n\n  // Autofill + date + time inputs\n  & > input[type]:-webkit-autofill:not(.browser-default):not([type=\"search\"]) + label,\n  & > input[type=date]:not(.browser-default) + label,\n  & > input[type=time]:not(.browser-default) + label {\n    transform: translateY(-14px) scale(.8);\n    transform-origin: 0 0;\n  }\n\n  .helper-text {\n    &::after {\n      opacity: 1;\n      position: absolute;\n      top: 0;\n      left: 0;\n    }\n\n    position: relative;\n    min-height: 18px;\n    display: block;\n    font-size: 12px;\n    color: rgba(0,0,0,.54);\n  }\n\n  // Prefix Icons\n  .prefix {\n    position: absolute;\n    width: $input-height;\n    font-size: $input-icon-size;\n    transition: color .2s;\n    top: ($input-height - $input-icon-size) / 2;\n\n    &.active { color: $input-focus-color; }\n  }\n\n  .prefix ~ input,\n  .prefix ~ textarea,\n  .prefix ~ label,\n  .prefix ~ .validate ~ label,\n  .prefix ~ .helper-text,\n  .prefix ~ .autocomplete-content {\n    margin-left: 3rem;\n    width: 92%;\n    width: calc(100% - 3rem);\n  }\n\n  .prefix ~ label { margin-left: 3rem; }\n\n  @media #{$medium-and-down} {\n    .prefix ~ input {\n      width: 86%;\n      width: calc(100% - 3rem);\n    }\n  }\n\n  @media #{$small-and-down} {\n    .prefix ~ input {\n      width: 80%;\n      width: calc(100% - 3rem);\n    }\n  }\n}\n\n\n/* Search Field */\n\n.input-field input[type=search] {\n  display: block;\n  line-height: inherit;\n  transition: .3s background-color;\n\n  .nav-wrapper & {\n    height: inherit;\n    padding-left: 4rem;\n    width: calc(100% - 4rem);\n    border: 0;\n    box-shadow: none;\n  }\n\n  &:focus:not(.browser-default) {\n    background-color: $input-background;\n    border: 0;\n    box-shadow: none;\n    color: #444;\n\n    & + label i,\n    & ~ .mdi-navigation-close,\n    & ~ .material-icons {\n      color: #444;\n    }\n  }\n\n  & + .label-icon {\n    transform: none;\n    left: 1rem;\n  }\n\n  & ~ .mdi-navigation-close,\n  & ~ .material-icons {\n    position: absolute;\n    top: 0;\n    right: 1rem;\n    color: transparent;\n    cursor: pointer;\n    font-size: $input-icon-size;\n    transition: .3s color;\n  }\n}\n\n\n/* Textarea */\n\n// Default textarea\ntextarea {\n  width: 100%;\n  height: $input-height;\n  background-color: transparent;\n\n  &.materialize-textarea {\n    line-height: normal;\n    overflow-y: hidden; /* prevents scroll bar flash */\n    padding: .8rem 0 .8rem 0; /* prevents text jump on Enter keypress */\n    resize: none;\n    min-height: $input-height;\n    box-sizing: border-box;\n  }\n}\n\n// For textarea autoresize\n.hiddendiv {\n  visibility: hidden;\n  white-space: pre-wrap;\n  word-wrap: break-word;\n  overflow-wrap: break-word; /* future version of deprecated 'word-wrap' */\n  padding-top: 1.2rem; /* prevents text jump on Enter keypress */\n\n  // Reduces repaints\n  position: absolute;\n  top: 0;\n  z-index: -1;\n}\n\n\n/* Autocomplete */\n.autocomplete-content {\n  li {\n    .highlight { color: #444; }\n\n    img {\n      height: $dropdown-item-height - 10;\n      width: $dropdown-item-height - 10;\n      margin: 5px 15px;\n    }\n  }\n}\n\n/* Character Counter */\n.character-counter {\n  min-height: 18px;\n}\n","/* Radio Buttons\n   ========================================================================== */\n\n// Remove default Radio Buttons\n[type=\"radio\"]:not(:checked),\n[type=\"radio\"]:checked {\n  position: absolute;\n  opacity: 0;\n  pointer-events: none;\n}\n\n[type=\"radio\"]:not(:checked) + span,\n[type=\"radio\"]:checked + span {\n  position: relative;\n  padding-left: 35px;\n  cursor: pointer;\n  display: inline-block;\n  height: 25px;\n  line-height: 25px;\n  font-size: 1rem;\n  transition: .28s ease;\n  user-select: none;\n}\n\n[type=\"radio\"] + span:before,\n[type=\"radio\"] + span:after {\n  content: '';\n  position: absolute;\n  left: 0;\n  top: 0;\n  margin: 4px;\n  width: 16px;\n  height: 16px;\n  z-index: 0;\n  transition: .28s ease;\n}\n\n/* Unchecked styles */\n[type=\"radio\"]:not(:checked) + span:before,\n[type=\"radio\"]:not(:checked) + span:after,\n[type=\"radio\"]:checked + span:before,\n[type=\"radio\"]:checked + span:after,\n[type=\"radio\"].with-gap:checked + span:before,\n[type=\"radio\"].with-gap:checked + span:after {\n  border-radius: 50%;\n}\n\n[type=\"radio\"]:not(:checked) + span:before,\n[type=\"radio\"]:not(:checked) + span:after {\n  border: 2px solid $radio-empty-color;\n}\n\n[type=\"radio\"]:not(:checked) + span:after {\n  transform: scale(0);\n}\n\n/* Checked styles */\n[type=\"radio\"]:checked + span:before {\n  border: 2px solid transparent;\n}\n\n[type=\"radio\"]:checked + span:after,\n[type=\"radio\"].with-gap:checked + span:before,\n[type=\"radio\"].with-gap:checked + span:after {\n  border: $radio-border;\n}\n\n[type=\"radio\"]:checked + span:after,\n[type=\"radio\"].with-gap:checked + span:after {\n  background-color: $radio-fill-color;\n}\n\n[type=\"radio\"]:checked + span:after {\n  transform: scale(1.02);\n}\n\n/* Radio With gap */\n[type=\"radio\"].with-gap:checked + span:after {\n  transform: scale(.5);\n}\n\n/* Focused styles */\n[type=\"radio\"].tabbed:focus + span:before {\n  box-shadow: 0 0 0 10px rgba(0,0,0,.1);\n}\n\n/* Disabled Radio With gap */\n[type=\"radio\"].with-gap:disabled:checked + span:before {\n  border: 2px solid $input-disabled-color;\n}\n\n[type=\"radio\"].with-gap:disabled:checked + span:after {\n  border: none;\n  background-color: $input-disabled-color;\n}\n\n/* Disabled style */\n[type=\"radio\"]:disabled:not(:checked) + span:before,\n[type=\"radio\"]:disabled:checked + span:before {\n  background-color: transparent;\n  border-color: $input-disabled-color;\n}\n\n[type=\"radio\"]:disabled + span {\n  color: $input-disabled-color;\n}\n\n[type=\"radio\"]:disabled:not(:checked) + span:before {\n  border-color: $input-disabled-color;\n}\n\n[type=\"radio\"]:disabled:checked + span:after {\n  background-color: $input-disabled-color;\n  border-color: $input-disabled-solid-color;\n}\n","/* Checkboxes\n   ========================================================================== */\n\n/* Remove default checkbox */\n[type=\"checkbox\"]:not(:checked),\n[type=\"checkbox\"]:checked {\n  position: absolute;\n  opacity: 0;\n  pointer-events: none;\n}\n\n// Checkbox Styles\n[type=\"checkbox\"] {\n  // Text Label Style\n  + span:not(.lever) {\n    position: relative;\n    padding-left: 35px;\n    cursor: pointer;\n    display: inline-block;\n    height: 25px;\n    line-height: 25px;\n    font-size: 1rem;\n    user-select: none;\n  }\n\n  /* checkbox aspect */\n  + span:not(.lever):before,\n  &:not(.filled-in) + span:not(.lever):after {\n    content: '';\n    position: absolute;\n    top: 0;\n    left: 0;\n    width: 18px;\n    height: 18px;\n    z-index: 0;\n    border: 2px solid $radio-empty-color;\n    border-radius: 1px;\n    margin-top: 3px;\n    transition: .2s;\n  }\n\n  &:not(.filled-in) + span:not(.lever):after {\n    border: 0;\n    transform: scale(0);\n  }\n\n  &:not(:checked):disabled + span:not(.lever):before {\n    border: none;\n    background-color: $input-disabled-color;\n  }\n\n  // Focused styles\n  &.tabbed:focus + span:not(.lever):after {\n    transform: scale(1);\n    border: 0;\n    border-radius: 50%;\n    box-shadow: 0 0 0 10px rgba(0,0,0,.1);\n    background-color: rgba(0,0,0,.1);\n  }\n}\n\n[type=\"checkbox\"]:checked {\n  + span:not(.lever):before {\n    top: -4px;\n    left: -5px;\n    width: 12px;\n    height: 22px;\n    border-top: 2px solid transparent;\n    border-left: 2px solid transparent;\n    border-right: $radio-border;\n    border-bottom: $radio-border;\n    transform: rotate(40deg);\n    backface-visibility: hidden;\n    transform-origin: 100% 100%;\n  }\n\n  &:disabled + span:before {\n    border-right: 2px solid $input-disabled-color;\n    border-bottom: 2px solid $input-disabled-color;\n  }\n}\n\n/* Indeterminate checkbox */\n[type=\"checkbox\"]:indeterminate {\n  + span:not(.lever):before {\n    top: -11px;\n    left: -12px;\n    width: 10px;\n    height: 22px;\n    border-top: none;\n    border-left: none;\n    border-right: $radio-border;\n    border-bottom: none;\n    transform: rotate(90deg);\n    backface-visibility: hidden;\n    transform-origin: 100% 100%;\n  }\n\n  // Disabled indeterminate\n  &:disabled + span:not(.lever):before {\n    border-right: 2px solid $input-disabled-color;\n    background-color: transparent;\n  }\n}\n\n// Filled in Style\n[type=\"checkbox\"].filled-in {\n  // General\n  + span:not(.lever):after {\n    border-radius: 2px;\n  }\n\n  + span:not(.lever):before,\n  + span:not(.lever):after {\n    content: '';\n    left: 0;\n    position: absolute;\n    /* .1s delay is for check animation */\n    transition: border .25s, background-color .25s, width .20s .1s, height .20s .1s, top .20s .1s, left .20s .1s;\n    z-index: 1;\n  }\n\n  // Unchecked style\n  &:not(:checked) + span:not(.lever):before {\n    width: 0;\n    height: 0;\n    border: 3px solid transparent;\n    left: 6px;\n    top: 10px;\n    transform: rotateZ(37deg);\n    transform-origin: 100% 100%;\n  }\n\n  &:not(:checked) + span:not(.lever):after {\n    height: 20px;\n    width: 20px;\n    background-color: transparent;\n    border: 2px solid $radio-empty-color;\n    top: 0px;\n    z-index: 0;\n  }\n\n  // Checked style\n  &:checked {\n    + span:not(.lever):before {\n      top: 0;\n      left: 1px;\n      width: 8px;\n      height: 13px;\n      border-top: 2px solid transparent;\n      border-left: 2px solid transparent;\n      border-right: 2px solid $input-background;\n      border-bottom: 2px solid $input-background;\n      transform: rotateZ(37deg);\n      transform-origin: 100% 100%;\n    }\n\n    + span:not(.lever):after {\n      top: 0;\n      width: 20px;\n      height: 20px;\n      border: 2px solid $secondary-color;\n      background-color: $secondary-color;\n      z-index: 0;\n    }\n  }\n\n  // Focused styles\n  &.tabbed:focus + span:not(.lever):after {\n    border-radius: 2px;\n    border-color: $radio-empty-color;\n    background-color: rgba(0,0,0,.1);\n  }\n\n  &.tabbed:checked:focus + span:not(.lever):after {\n    border-radius: 2px;\n    background-color: $secondary-color;\n    border-color: $secondary-color;\n  }\n\n  // Disabled style\n  &:disabled:not(:checked) + span:not(.lever):before {\n    background-color: transparent;\n    border: 2px solid transparent;\n  }\n\n  &:disabled:not(:checked) + span:not(.lever):after {\n    border-color: transparent;\n    background-color: $input-disabled-solid-color;\n  }\n\n  &:disabled:checked + span:not(.lever):before {\n    background-color: transparent;\n  }\n\n  &:disabled:checked + span:not(.lever):after {\n    background-color: $input-disabled-solid-color;\n    border-color: $input-disabled-solid-color;\n  }\n}\n","/* Switch\n   ========================================================================== */\n\n.switch,\n.switch * {\n  -webkit-tap-highlight-color: transparent;\n  user-select: none;\n}\n\n.switch label {\n  cursor: pointer;\n}\n\n.switch label input[type=checkbox] {\n  opacity: 0;\n  width: 0;\n  height: 0;\n\n  &:checked + .lever {\n    background-color: $switch-checked-lever-bg;\n\n    &:before, &:after {\n      left: 18px;\n    }\n\n    &:after {\n      background-color: $switch-bg-color;\n    }\n  }\n}\n\n.switch label .lever {\n  content: \"\";\n  display: inline-block;\n  position: relative;\n  width: 36px;\n  height: 14px;\n  background-color: $switch-unchecked-lever-bg;\n  border-radius: $switch-radius;\n  margin-right: 10px;\n  transition: background 0.3s ease;\n  vertical-align: middle;\n  margin: 0 16px;\n\n  &:before, &:after {\n    content: \"\";\n    position: absolute;\n    display: inline-block;\n    width: 20px;\n    height: 20px;\n    border-radius: 50%;\n    left: 0;\n    top: -3px;\n    transition: left 0.3s ease, background .3s ease, box-shadow 0.1s ease, transform .1s ease;\n  }\n\n  &:before {\n    background-color: transparentize($switch-bg-color, .85);\n  }\n\n  &:after {\n    background-color: $switch-unchecked-bg;\n    box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2), 0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12);\n  }\n}\n\n// Switch active style\ninput[type=checkbox]:checked:not(:disabled) ~ .lever:active::before,\ninput[type=checkbox]:checked:not(:disabled).tabbed:focus ~ .lever::before {\n  transform: scale(2.4);\n  background-color: transparentize($switch-bg-color, .85);\n}\n\ninput[type=checkbox]:not(:disabled) ~ .lever:active:before,\ninput[type=checkbox]:not(:disabled).tabbed:focus ~ .lever::before {\n  transform: scale(2.4);\n  background-color: rgba(0,0,0,.08);\n}\n\n// Disabled Styles\n.switch input[type=checkbox][disabled] + .lever {\n  cursor: default;\n  background-color: rgba(0,0,0,.12);\n}\n\n.switch label input[type=checkbox][disabled] + .lever:after,\n.switch label input[type=checkbox][disabled]:checked + .lever:after {\n  background-color: $input-disabled-solid-color;\n}\n","/* Select Field\n   ========================================================================== */\n\nselect { display: none; }\nselect.browser-default { display: block; }\n\nselect {\n  background-color: $select-background;\n  width: 100%;\n  padding: $select-padding;\n  border: $select-border;\n  border-radius: $select-radius;\n  height: $input-height;\n}\n\n.select-label {\n  position: absolute;\n}\n\n.select-wrapper {\n  &.valid .helper-text[data-success],\n  &.invalid ~ .helper-text[data-error] {\n    @extend %hidden-text;\n  }\n\n  &.valid {\n    & > input.select-dropdown {\n      @extend %valid-input-style;\n    }\n\n    & ~ .helper-text:after {\n      @extend %custom-success-message;\n    }\n  }\n\n  &.invalid {\n    & > input.select-dropdown,\n    & > input.select-dropdown:focus {\n      @extend %invalid-input-style;\n    }\n\n    & ~ .helper-text:after {\n      @extend %custom-error-message;\n    }\n  }\n\n  &.valid + label,\n  &.invalid + label {\n    width: 100%;\n    pointer-events: none;\n  }\n\n  & + label:after {\n    @extend %input-after-style;\n  }\n\n  position: relative;\n\n  input.select-dropdown {\n    &:focus {\n      border-bottom: 1px solid $input-focus-color;\n    }\n    position: relative;\n    cursor: pointer;\n    background-color: transparent;\n    border: none;\n    border-bottom: $input-border;\n    outline: none;\n    height: $input-height;\n    line-height: $input-height;\n    width: 100%;\n    font-size: $input-font-size;\n    margin: $input-margin;\n    padding: 0;\n    display: block;\n    user-select:none;\n    z-index: 1;\n  }\n\n  .caret {\n    position: absolute;\n